.notfound {
  background-color: #f5f6f7;
  max-width: 1440px;
  height: 374px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.notfound__img {
}

.notfound__title {
  font-family: "Robotoslab regular", Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 26px;
  line-height: 30px;
  margin: 24px 0 16px 0;
}

.notfound__paragraph {
  max-width: 340px;
  font-family: inherit;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 24px;
  color: #b6bcbf;
  text-align: center;
  margin: 0;
}
